  src: url("../fonts/roboto/Roboto-Bold-webfont.woff") format("truetype"); }

body {
  background: #f1f4f9;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  color: #666666;
  font-size: 16px;
  min-height: 100vh; }

input,
select,
textarea {
  font-size: 14px; }

hr {
  border-top: 1px #e6e6e6 solid;
  margin: 20px 0; }

ul {
  padding: 0;
  list-style: none; }

a {
  color: #4a75b5;
  text-decoration: none; }
  a:hover, a:focus {
    text-decoration: none; }
  a[ng-href], a[ng-click] {
    cursor: pointer; }
  a.white, a.white:hover, a.white:active, a.white:focus {
    color: white; }

i.white, i.white:hover, i.white:active, i.white:focus {
  color: white; }

.application {
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  padding-top: 50px; }
  .application.wrapper {
    position: relative; }
    .application.wrapper .wrapper-content {
      padding-bottom: 35px; }
    .application.wrapper .wrapper-footer {
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      text-align: center;
      max-height: 35px;
      overflow: hidden; }
  .application.fullscreen {
    padding-top: 0; }
    .application.fullscreen .wrapper-content {
      display: flex;
      flex-grow: 1;
      padding-bottom: 0; }
    .application.fullscreen .container {
      width: 100%;
      padding: 0;
      margin: 0; }

#menu,
.footer,
.header:not(th) {
  width: 100%;
  -webkit-flex: 0;
  flex: 0; }

/* TODO: Mover classes para seus devidos arquivos */
.last-update {
  color: #737373;
  font-style: italic;
  font-size: 13px; }

.data-value {
  display: table-cell;
  height: 40px;
  vertical-align: middle;
  font-size: 14px;
  font-weight: 400; }

.data-link {
  display: table-cell;
  height: 62px;
  padding-top: 18px;
  vertical-align: middle;
  font-size: 14px; }

.line-label {
  display: block;
  margin-top: 22px;
  height: 40px;
  overflow: hidden;
  background-image: url("../img/bg-line.png");
  background-position: center, left;
  background-repeat: repeat-x;
  padding-top: calc(13px / 2);
  font-size: 13px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: #999999;
  text-overflow: ellipsis;
  white-space: nowrap; }
  .line-label .text {
    background-color: white;
    padding: 0 20px; }

.captcha {
  width: 100%;
  height: auto;
  max-height: 84px;
  border: 1px #e6e6e6 solid;
  text-align: center; }
  .captcha img {
    max-width: 100%;
    max-height: 100%; }

